<template>
    <Loader v-if='loading'/>
    <form v-else id="form" @submit.prevent="submitHandler">
    <div class="input-field">
        <input
            id="limit"
            type="text"
            v-model="limit"
            :class="{invalid: $v.limit.$dirty && !$v.limit.minValue}"
        >
        <label for="limit">Сумма, руб</label>
        <span 
            v-if="$v.limit.$dirty && !$v.limit.minValue"
            class="helper-text invalid" 
        >
            Введите сумму более {{$v.limit.$params.minValue.min}}
        </span>
    </div> 
    <div class="input-field " >
        <select 
            ref="select" 
            v-model="selectOption"
        >
            <option
              v-for="cur of currencies"
              :key="cur"
            >
                {{cur}}
            </option>
        </select>
        <label>Валюта для конвертации</label>
    </div>
    <div class="input-field summ">
        <label>Сумма в валюте</label>
        <p class="summ_val"
            v-if="this.selectOption"
        >
            {{
                (this.base* this.rates[`${this.selectOption}`]).toFixed(5) 
            }}
        </p>
            

        
    </div> 
    <div class="input-field">
        <input
            id="email"
            type="text"
            v-model.trim="email"
            :class="{invalid: ($v.email.$dirty && !$v.email.required) || ($v.email.$dirty && !$v.email.email)}"
        >
        <label for="email">Email</label>
        <small 
            class="helper-text invalid"
            v-if="$v.email.$dirty && !$v.email.required"
        >
            Поле Email не должно быть пустым
        </small>
        <small 
            class="helper-text invalid"
            v-else-if="$v.email.$dirty && !$v.email.email"
        >
            Введите корректный Email
        </small>
    </div>  
    <button
        class="btn waves-effect waves-light auth-submit"
        type="submit"
    >
        Записаться на обмен валюты
        <i class="material-icons right">send</i>
    </button>
  </form>
</template>

<script>
import {email, required, minValue} from '/node_modules/vuelidate/lib/validators'
import axios from 'axios'

export default {
  name: 'HomeForm',
  props: ['rates'],
  data: () => ({
    currencies: ['RUB', 'USD', 'EUR'],
    email: '',
    limit: 10,
    summ:  '',
    selectOption: '',
    loading: false
  }),
  computed:{
      base() {
          return this.limit / (this.rates['RUB'] / this.rates['EUR'])
      }
  },
  validations: {
        email: {email, required},
        limit: {minValue: minValue(10)}
  },
  mounted() {
        this.select = M.FormSelect.init(this.$refs.select);
        M.updateTextFields();
    
  },
  methods: {
      
    async submitHandler() {
        if (!this.selectOption){
            document.querySelector('.select-wrapper').classList.add('invalid')
        }else document.querySelector('.select-wrapper').classList.remove('invalid')
        if (this.$v.$invalid) {
            this.$v.$touch()
            return
        }
        
        this.loading = true;
        const summ = (this.base* this.rates[`${this.selectOption}`]).toFixed(5);
        const formData = {
             email : this.email,
             limit : this.limit,
             summ  : summ 
        }
        // console.log(formData)
        axios.post(
            '/sendmail.php', formData)
        .then(function(res){
            console.log(res)
            M.toast({html: 'Заявка отправлена!'})
        })

        .catch(function(error){
            M.toast({html: 'Ошибка при отправке данных!'})
            console.log(error);
        })
        setTimeout(() => {
            this.loading = false; 
            setTimeout(()=>{
                this.email = '';   
                this.limit = 10;
                this.$v.$reset();
                this.select = M.FormSelect.init(this.$refs.select);
                M.updateTextFields();     
            }, 0)   
        }, 200)
        
           
    }
  }
}
</script>

<style lang="scss" scoped>
    #form{
        padding: 40px 20px;
        width: 440px;
        height: 550px;
        background: rgb(255, 255, 255);
        border-radius: 0 50px 50px 50px;
        box-shadow: 10px 10px 60px #E2E7FF;
        display: flex;
        flex-direction: column;
    }
    .input-field{
        margin: 20px 0;
    }
    .summ{
        border-bottom: 1px solid #9e9e9e;
        font-size: 16px;
        p{
            margin: 0 0 10px 0;
        }
        label{
            position: relative;
        }
    }
    .btn{
        margin: 40px 0 0 0;
    }
    
    
</style>