<template>
  <Loader v-if='loading'/>

  <HomeForm 
    v-else
    :rates="currency.rates" 
  />
</template>

<script>
import HomeForm from '@/components/HomeForm'

export default {
  name: 'Home',
  data: () => ({
    currency: null,
    loading: true
  }),
  async mounted() {
    this.currency = await this.$store.dispatch('fetchCurrency');
    this.loading = false;
  },
  components:{
    HomeForm
  }
}
</script>
